import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAngleDoubleUp, faArrowLeft, faPlus, faTimes, faTools, faUpload } from '@fortawesome/free-solid-svg-icons';
import { NavBar, NavBarBrand, DataEpysa, NoImage, ShowDato, toMoney, NavBarMenu, Input, Dropdown, DropdownTrigger, DropDownMenu } from './utils';
import { Link } from "react-router-dom";
import cloneDeep from 'lodash/cloneDeep';
import * as conf from './config.json';

export default class Details extends React.Component
{
  constructor(props)
  {
    super();
    this.data_source = new DataEpysa(conf.DataEpysa);
    this.state =
    {
      error                 : null,
      data                  : { modelos_aplicable : [] },
      isLoaded              : false,
      edit                  : false,
      modelos               : [],
      suggestModelos        : [],
      imageTituloPreview    : null,
      uploadedImages        : [{id : 0}],
      savedImages           : [],
      admins                : [],
      lastUpload            : 0,
      preview               : -1,
      previewSaved          : -1,
      guardando             : false,
      subiendo              : false
    }
  }

  componentDidMount(){
    this.data_source.exec(
      'PRO_FICHA_REPUESTO',
      { producto : this.props.producto }
    )
      .then (res => res.json())
      .then (
        result => {
          //console.log(result.data);
          if ( result.data && ! result.error  )
          {
            result.data[0].modelos_aplicable = result.data[0].modelos_aplicable ? JSON.parse( result.data[0].modelos_aplicable ) : [];
            this.setState({
              isLoaded  : true,
              data      : result.data[0]
            }, () => this.cargarImagenes() );

          }
          else
            this.setState({ isLoaded  : true });

        },
        error => {
          this.setState({
            isLoaded  : true,
            error
          });
        }
      );

    this.data_source.exec(
      'PAR_GET_PARAMETRO',
      { parametro : 'FCHRPADM' }
    )
      .then (res => res.json())
      .then (
        result => {
          //console.log(result.data);
          if ( result.data && ! result.error  )
          {
            this.setState({
              admins      : result.data[0].Valor_texto ? JSON.parse(result.data[0].Valor_texto) : []
            });
          }
          else
            console.log(result)

        },
        error => {
          this.setState({
            isLoaded  : true,
            error
          });
        }
      );
    
    

    this.data_source.exec(
      'PRO_LISTA_MODELOS_BUSES',
      { producto : this.props.producto }
    )
      .then (res => res.json())
      .then (
        result => {
          //console.log(result.data);
          if ( result.data && ! result.error  )
          {
            this.setState({ modelos : result.data });
          }
        },
        error => {
          console.log(error);
          this.setState({
            error
          });
        }
      );
  }

  cargarImagenes = () =>{
    console.log(this.state.data);
    this.data_source.exec(
      'PRO_IMAGENES_FICHA_REPUESTO',
      { producto : this.state.data.Producto_proveedor_habitual !== "null" ? this.state.data.Producto_proveedor_habitual : this.props.producto  }
    )
      .then (res => res.json())
      .then (
        result => {
          //console.log(result.data);
          if ( result.data && ! result.error  ){
            this.setState({ savedImages : result.data });
          }
        },
        error => {
          console.log(error);
          this.setState({
            error
          });
        }
      );
  }
  
  editar            = () => this.setState( state => ( { oldDato : cloneDeep(state.data), edit : true } ) )

  cancelEdit        = () => this.setState( state => ( { data : state.oldDato, edit : false, uploadedImages : [{id : 0}], lastUpload : 0 } ) )

  setSuggest        = (e) => this.setState( { newModel : e.target.id } );

  deleteImage       = (id) => { this.setState( state => ( { uploadedImages : state.uploadedImages.filter( e => e.id !== id ) } ) ) }

  deleteImageSaved  = (id) => { this.setState( state => ( { savedImages : state.savedImages.map( e => { 
    if ( e.DPR_ID === id ) 
      e.deleted = "S"; 
    return e  } ) } ) ) }


  changeSuggest = (e) => {
    const t       = e.target;
    if ( t.value.length > 2 )
      this.setState({ newModel : t.value, suggestModelos : this.state.modelos.filter ( item => item.modelo.toLowerCase().includes(t.value.toLowerCase()) ) });
    else 
      this.setState({ newModel : t.value, suggestModelos : [] });
  }

  setDato = e =>{ this.setState( state => {

    const newDato = cloneDeep( state.data ) ;
    const t       = e.target;

    newDato[t.name] = t.value;

    return { data : newDato }

  })}

  deleteModelo = (modelo) => {
    const newDato = cloneDeep( this.state.data ) ;
    newDato.modelos_aplicable = newDato.modelos_aplicable.filter( e => e !== modelo )
    this.setState({ data : newDato })
  }

  addModelo = (e) => { 
    e.preventDefault();
    if ( this.state.newModel.length > 1 )
    {
      this.setState( state => {
        const newDato = cloneDeep( state.data ) ;
        newDato.modelos_aplicable.push( state.newModel );
        return { data : newDato, newModel : "", suggestModelos : [] }
      })
    }
  }

  uploadImage = (e) => {
    
    const reader = new FileReader();
    const file = e.target.files[0];
    const name = e.target.name;

    if (file){

      reader.onloadend = () => {
        this.setState(state=>{

          if ( name === "imageTituloUpLoad" )
            return { imageTituloPreview: reader.result }
          else
          {
            const newUpImages = cloneDeep(state.uploadedImages);
  
            newUpImages[state.lastUpload].preview = reader.result;
            newUpImages.push( { id: state.lastUpload + 1 } )
            
            return { uploadedImages : newUpImages, lastUpload : state.lastUpload + 1 } 

          }

        });
      }

      reader.readAsDataURL(file);
    }
    
  }

  guardar = e => {
    e.preventDefault();
    this.setState( { guardando : true, subiendo : true, edit : false  } );
    const data = new FormData(e.target);

/*     console.log( typeof this.state.data.modelos_aplicable );
    console.log( typeof this.state.data ); */

    this.data_source.exec(
      'PRO_GRABA_FICHA_REPUESTO',
       this.state.data 
    )
      .then (res => res.json())
      .then (
        result => {
          //console.log(result.data);
          if ( result.data && ! result.error  )
            this.setState({ guardando : false });
        },
        error => {
          console.log(error);
          this.setState({
            error
          });
        }
      );

      this.state.savedImages.filter( i => i.deleted && i.deleted === "S" )
      .forEach( i => {

          this.data_source.exec(
            'PRO_FICHA_REPUESTO_BORRA_IMAGEN',
            { id : i.DPR_ID }
          )
            .then (res => res.json())
            .then (
              result => {
                //console.log(result.data);
                if ( result.error )
                  console.log(result);
              },
              error => {
                //console.log(error);
                this.setState({
                  error
                });
              }
            );

      } );

    fetch(conf.upload, {
      method: 'POST',
      body: data,
    })
      .then (
        resp => {
          if (resp.ok){
            this.setState( { uploadedImages : [{id : 0}], lastUpload : 0, subiendo : false }, this.cargarImagenes() );
            //this.setState();
          }
          else
            console.log(resp);

          this.data_source.exec(
            'PRO_FICHA_REPUESTO_BORRA_IMAGEN',
            { producto : this.state.data.Producto_proveedor_habitual }
          )
            .then (res => res.json())
            .then (
              result => {
                //console.log(result.data);
                if ( result.error  )
                  console.log(result);
              },
              error => {
                //console.log(error);
                this.setState({
                  error
                });
              }
            );

        },
        error => console.log(error)
      );
  } 

  render()
  {
    const { data, 
            edit, 
            suggestModelos, 
            newModel, 
            imageTituloPreview, 
            savedImages, 
            uploadedImages, 
            lastUpload, 
            preview, 
            previewSaved,
            guardando, 
            subiendo,
            admins }  

              = this.state;

    const imagenesExtra = savedImages.filter (i => i.DPR_Seleccion === "N" && ( !i.deleted || i.deleted !== "S" ) );
    const uploading     = guardando || subiendo ;
    const admin         = admins.includes(this.props.user);
    const ubicacion     = data.ubicacion ? JSON.parse(data.ubicacion) : null;

    return  <>
              <NavBar>
                  <NavBarBrand noBurger={ ( edit || uploading ) ? true : false }>
                    <>
                      <div className="navbar-item" >
                      { ( !edit && !uploading ) &&
                          <Link to="/" className="has-text-white-bis">
                            <FontAwesomeIcon icon={faArrowLeft} size="lg" />
                          </Link>
                      }
                      </div>
                      <div className="navbar-item" >
                        <span className="icon is-medium">
                          <FontAwesomeIcon size="lg" icon={faTools}/>
                        </span>
                        <span>Repuesto</span>
                      </div>
                      { ( edit || uploading )  &&
                          <div className="buttons">
                            <button type="submit" form="form1" className={"button is-info" + ( uploading ? " is-loading" : "" ) } disabled={uploading} >
                              Guardar
                            </button>
                            <button className="button is-danger" onClick={this.cancelEdit} disabled={uploading}>
                              Cancelar
                            </button>
                          </div>
                      }
                    </>
                </NavBarBrand>
                <NavBarMenu toggleOnClick={true}>
                  <div className="navbar-end">
                    <div className="navbar-item">
                      { ! edit &&
                          <button className="button is-info" onClick={this.editar}>
                            Editar
                          </button>
                      }
                    </div>
                  </div>
                </NavBarMenu>
              </NavBar>
              <section className="section">
                <div className="container">
                  <form method="POST" encType="multipart/form-data" id="form1" onSubmit={this.guardar}>
                    <input type="hidden" value={data.Producto_proveedor_habitual !== "null" ? data.Producto_proveedor_habitual : data.Producto } name="producto" />
                    <div className="cont-flex">
                      <div className="titulo">
                        <h1 className="title is-5">{data.Producto}</h1>
                        { edit && admin ?
                            <textarea 
                              className="textarea is-small has-fixed-size" 
                              name="Nombre" 
                              rows="3" 
                              value={data.Nombre} 
                              maxLength="1000"
                              onChange={this.setDato}  /> 
                          :
                            <h2 className="subtitle is-6 has-text-centered">{data.Nombre}</h2>
                        }
                        
                        <div className="image-titulo-container">
                          { imageTituloPreview ?
                              <img className="image-titulo" src={imageTituloPreview} alt="imagen principal" onClick={ () => this.setState({ preview : 9999}) } />
                            : 
                            savedImages.length > 0 && savedImages[0].DPR_Seleccion === "T" ?
                              <img className="image-titulo" src={conf.showFile + savedImages[0].DPR_ID } alt={savedImages[0].DPR_Nombre} onClick={ () => this.setState({ previewSaved : savedImages[0].DPR_ID})} />
                            :
                              <NoImage />
                          }
                          { edit &&
                              <div className="field image-upload">
                                <div className="file is-small is-primary is-boxed">
                                  <label className="file-label">
                                    <input className="file-input" type="file" name="imageTituloUpLoad" onChange={this.uploadImage} accept="image/*" />
                                    <span className="file-cta">
                                      <span className="file-icon">
                                        <FontAwesomeIcon icon={faUpload} size="2x" />
                                      </span>
                                    </span>
                                  </label>
                                </div>
                              </div>
                          }
                        </div>
                      </div>
                      <div className="datos">
                        <p className="title is-6">Datos</p>
                        <div className="columns is-mobile">
                          <ShowDato heading="N Parte" className="column is-half">
                              { data.Producto_proveedor_habitual !== "null" ? data.Producto_proveedor_habitual : "Sin numero parte" }
                          </ShowDato>
                          <ShowDato heading="Costo" className="column is-half">
                            $ {toMoney(data.costo)}
                          </ShowDato>
                        </div>
                        <div className="columns is-mobile">
                          <ShowDato heading="Stock" className="column is-half">
                            {data.stock}
                          </ShowDato>
                          <ShowDato heading="Fecha ult. compra" className="column is-half">
                            {data.fecha}
                          </ShowDato>
                        </div>
                        <div className="columns is-mobile">
                          
                          <ShowDato heading="Modelos buses" className="column is-full is-flex">
                            { data.modelos_aplicable
                                .map( e => edit ?
                                            <div className="control mr-1 mb-1" key={e}>
                                              <div className="tags has-addons">
                                                <span className="tag is-delete" onClick={ () => this.deleteModelo(e) }></span>
                                                <span className="tag is-primary is-light">{e}</span>
                                              </div>
                                            </div>
                                          :
                                            <span key={e} className={"tag is-primary is-light mr-1 mb-1"}>
                                              {e}
                                            </span> )}
                            { edit &&
                                <Dropdown>
                                  {(props) =>
                                    <>
                                      <div className="is-flex mt-2">
                                        <DropdownTrigger {...props}>
                                          <Input 
                                            name="newModel"
                                            value={newModel}
                                            onChange={this.changeSuggest} />
                                        </DropdownTrigger>
                                        <button name="btnAdd" className="button is-small btn-add is-primary" onClick={this.addModelo}>
                                          <FontAwesomeIcon icon={faAngleDoubleUp} size="sm" />
                                        </button>
                                      </div>                                    
                                      <DropDownMenu {...props} onSelect={this.setSuggest}>
                                        { suggestModelos.map( i => 
                                          <div id={i.modelo} className="dropdown-item" key={i.modelo} >
                                            {i.modelo}
                                          </div>) }
                                      </DropDownMenu>
                                    </>
                                  }
                                </Dropdown>
                              }
                          </ShowDato>
                        </div>
                        { ubicacion &&
                          <div className="columns is-mobile">
                            <ShowDato heading="Ubicacin" className="column is-full is-flex">
                              { ubicacion.map( u => u.ubicacion ) }
                            </ShowDato>
                          </div>
                        }
                      </div>
                    </div>
                    <div className="descripcion">
                      <p className="title is-6">Descripcin</p>
                      { edit ?
                          <textarea
                            name="Descripcion_detallada"
                            className="textarea is-small"
                            rows="4" 
                            value={data.Descripcion_detallada} 
                            maxLength="4090"
                            onChange={this.setDato} />
                        :
                          <p className="is-size-6">{data.Descripcion_detallada ? data.Descripcion_detallada : "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed eiusmod tempor incidunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquid ex ea commodi consequat. Quis aute iure reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint obcaecat cupiditat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum."}</p>
                      }
                      
                    </div>
                    <div className="imagenes is-flex is-flex-wrap-wrap">
                      { imagenesExtra.length === 0 && uploadedImages.length === 1 && ! edit &&
                          <>
                            <div className="image">
                              <NoImage />
                            </div>
                            <div className="image">
                              <NoImage />
                            </div>
                            <div className="image">
                              <NoImage />
                            </div>
                          </>
                      }
                      { imagenesExtra
                          .map( i =>  <div className="image" key={i.DPR_ID}>
                                        <img className="image-extra" src={conf.showFile + i.DPR_ID } alt={i.DPR_Nombre} onClick={ () => this.setState({ previewSaved : i.DPR_ID}) } />
                                        { edit &&
                                          <div className="image-upload">
                                            <button className="button is-small is-danger" onClick={ () => this.deleteImageSaved(i.DPR_ID) }>
                                              <FontAwesomeIcon icon={faTimes} />
                                            </button>
                                          </div> }
                                      </div> ) }
                      { uploadedImages
                          .map( i =>  <div className="image" key={i.id}>
                                        { edit ?
                                            <>
                                            <img className={"image-extra " + ( i.id === lastUpload ? "is-hidden" : "" )}  src={i.preview} alt="imagen secundaria" />
                                            <div className={"image-upload " + ( i.id === lastUpload ? "is-hidden" : "" )}>
                                              <button className="button is-small is-danger" onClick={ () => this.deleteImage(i.id) }>
                                                <FontAwesomeIcon icon={faTimes} />
                                              </button>
                                            </div>
                                            <div className={"field image-upload " + ( i.id !== lastUpload ? "is-hidden" : "" )}>
                                              <div className="file is-small is-primary is-boxed">
                                                <label className="file-label">
                                                  <input className="file-input" type="file" name={"imageUpLoad_" + i.id} onChange={this.uploadImage} accept="image/*" />
                                                  <span className="file-cta">
                                                    <span className="file-icon">
                                                      <FontAwesomeIcon icon={faPlus} size="2x" />
                                                    </span>
                                                  </span>
                                                </label>
                                              </div>
                                            </div>
                                            </>
                                          :
                                            <img className={"image-extra " + ( i.id === lastUpload ? "is-hidden" : "" )} src={i.preview} alt="imagen secundaria" onClick={ () => this.setState({ preview : i.id}) } />
                                        }
                                      </div> ) } 
                      </div>
                    <div className={"modal " + ( preview > -1 || previewSaved > -1 ? "is-active" : "" )} onClick={ () => this.setState({ preview : -1, previewSaved : -1}) } >
                      <div className="modal-background"></div>
                      <div className="modal-content">
                        { preview > -1 &&
                            <img src={ preview === 9999 ? imageTituloPreview : uploadedImages.filter(e => e.id === preview)[0].preview} alt="imagen modal" /> }
                        { previewSaved > -1 &&
                            <img src={conf.showFile + previewSaved } alt="imagen modal" />
                        }
                      </div>
                    </div>
                  </form>
                </div>
              </section>
            </>
  }

}