import "react-app-polyfill/ie11";
import "react-app-polyfill/stable";
import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import Details from './Details';
import Login from './Login';
import reportWebVitals from './reportWebVitals';

import {
  BrowserRouter as Router,
  Redirect,
  Switch,
  Route
} from "react-router-dom";
import Cookies from 'universal-cookie';

export default function Main(props) {
  const cookies = new Cookies();
  const urlParams = new URLSearchParams(window.location.search);
  const wv_id = urlParams.get('wv_id') ? urlParams.get('wv_id') : null;
  const wv_ps = urlParams.get('wv_ps') ? urlParams.get('wv_ps') : null;
  

  let redirect;
  
  if ( cookies.get("login_state") ){
    let expiryDate = new Date();
    expiryDate.setMonth(expiryDate.getMonth() + 6);
    cookies.set("login_state", true, { path: '/', expires : expiryDate  });
    cookies.set("user", cookies.get("user"), { path: '/', expires : expiryDate  });
    cookies.set("nombre", cookies.get("nombre"), { path: '/', expires : expiryDate  });
  }
  else
    redirect = <Redirect to="/login" />;
  
  return (
    <Router basename=".">
        <Switch>
          <Route path="/about">
            <About />
          </Route>
          <Route path="/login">
            <Login wv_id={wv_id} wv_ps={wv_ps} />
          </Route>
          <Route path="/detalle">
            <Detalle user={cookies.get("user")} />
          </Route>
          <Route path="/">
            <App />
          </Route>
        </Switch>
      {redirect}
    </Router>
  );
}

function About() {
  return <h2>Version 1.1</h2>;
}

function Detalle(props)
{
  const urlParams = new URLSearchParams(window.location.search);
  const p         = urlParams.get('pro') ?  urlParams.get('pro') : ''  ;

  return <Details user={props.user} producto={p} />
}


ReactDOM.render(
  <React.StrictMode>
    <Main />
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals(console.log);
