
import React, { useState } from 'react';
import './App.sass';
import {DataEpysa, Cargando, NavBar, NavBarBrand, NavBarMenu, Input, NoImage} from './utils';
import Cookies from 'universal-cookie';
import { Redirect, Link } from "react-router-dom";
import * as conf from './config.json';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSearch, faTools } from '@fortawesome/free-solid-svg-icons';

export default class App extends React.Component 
{
  constructor(props)
  {
    super();
    this.data_source = new DataEpysa(conf.DataEpysa);
    this.cookies = new Cookies();
    this.state =
    {
      error         : null,
      data          : [],
      redirect      : null,
      isLoaded      : false,
      termino       : ''
    }
  }

  componentDidMount(){

    this.data_source.exec(
      'PRO_LISTA_FICHA_REPUESTO',
      { top : 15 }
    )
      .then (res => res.json())
      .then (
        result => {
          //console.log(result.data);
          this.setState({
            isLoaded  : true,
            data      : result.data ? result.data : []
          });
        },
        error => {
          this.setState({
            isLoaded  : true,
            error
          });
        }
      );
  }

  setFiltro = e =>{

    const termino = e.target.value;
    this.setState({isLoaded : false, termino : termino});

    if ( termino.length > 2 ) {
      this.data_source.abort();
      this.data_source.exec(
        'PRO_LISTA_FICHA_REPUESTO',
        { top : 20, termino : termino }
      )
        .then (res => res.json())
        .then (
          result => {
            if(result.data){
              //console.log(result.data);
              this.setState({
                data      : result.data,
                isLoaded  : true
              });
            }
            else
              this.setState({
                data      : [],
                isLoaded  : true
              });
          }
        );
    }

    if ( termino === "" ) {
      this.data_source.abort();
      this.data_source.exec(
        'PRO_LISTA_FICHA_REPUESTO',
        { top : 15 }
      )
        .then (res => res.json())
        .then (
          result => {
            if(result.data){
              //console.log(result.data);
              this.setState({
                data      : result.data,
                isLoaded  : true
              });
            }
            else
              this.setState({
                data  : [],
                isLoaded  : true
              });
          }
        );
    }
  }

  cerrarSesion = () => {
    this.cookies.remove("login_state", { path: '/'});
    this.cookies.remove("user", { path: '/'});
    this.cookies.remove("nombre", { path: '/'});
    this.setState({redirect : "logout"});
  }

  render()
  {
    const { error, data, isLoaded, termino, redirect } = this.state;

    if (error)
      return <div className="app-error">Error: {error.message}</div>;
    if (redirect === "logout")      
      return <Redirect to="/login" />;
    else
      return <>
        <NavBar>
          <NavBarBrand>
            <div className="navbar-item" >
              <span className="icon is-medium">
                <FontAwesomeIcon size="lg" icon={faTools}/>
              </span>
              <span>Repuestos</span>
            </div>
            <div className="navbar-item" >
              <Input onChange={this.setFiltro} size="16" icon={faSearch} value={termino} placeholder="Buscar" type="text" />
            </div>
          </NavBarBrand>
          <NavBarMenu closeOnClick="true" >
            <div className="navbar-end">              
              <div className="navbar-item has-dropdown is-hoverable">
                <p className="navbar-link">{ this.cookies.get("nombre") }</p>
                <div className="navbar-dropdown">
                  <div className="navbar-item">
                    <div className="buttons">
                      <button className="button is-danger is-light is-small" onClick={this.cerrarSesion}>
                        Cerrar Sesion
                      </button>
                    </div>
                  </div>
                </div>
              </div>

              <div className="navbar-item">
                <div onClick={ () => window.location.reload() } className="has-text-grey-lighter" >
                  v16
                </div>
              </div>
              
            </div>
        </NavBarMenu>
        </NavBar>
        <section className="section">
          <div className="container pt-2">
            { isLoaded ?
                data.map( e => <ListItem item={e} key={e.Producto} /> ) 
              :
              <Cargando /> }
          </div>
        </section>
      </>;  
    }
  
}

function ListItem(props)
{
  const item          = props.item;

  return  <Link to={"/detalle?pro=" + item.Producto} >
            <div className="box is-flex mb-3"> 
              <div className="list-image" >
                <Image producto={item.Producto_proveedor_habitual !== "null" ? item.Producto_proveedor_habitual : item.Producto } />
              </div>
              <div className="list-content" >
                <div className="box-row is-size-7" >
                  <strong>{item.Producto}</strong>
                </div>
                <div className="box-row is-size-7" >
                  {item.Nombre}
                </div>
              </div>
            </div>
          </Link>
}


function Image(props){
  const [ dprId, setDprId ] = useState();

  useState( () =>{
    const DataSource = new DataEpysa(conf.DataEpysa);
    DataSource.exec(
      'PRO_GET_DPR_ID',
      { producto : props.producto } )
      .then (res => res.json())
      .then (
        result => {
          //console.log(result.data);
        if ( result.data )
          setDprId(result.data[0].DPR_ID);
        else
          console.log(result);
        },
        error => { console.log("error lista archivos") }
      )
  }
  , [props.producto] )

  return dprId ? 
            <img className="image-extra" src={ conf.showFile + dprId } alt="imagen principal" />
          :
            <NoImage />
}

