import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSpinner, faCircle, faImage } from '@fortawesome/free-solid-svg-icons'

function Input(props)
{
  return (
    <div className="field">
      <p className={"control " + (props.icon ? "has-icons-right" : "")}>
        <input 
          className={"input is-small " + props.addClass + " " + (props.type === "number" ? "has-text-right" : "")} 
          onChange={props.onChange} 
          value={props.value} 
          disabled={props.disabled}
          type={props.type === "number" ? "text" : props.type }     
          inputMode={ props.type === "number" ? "numeric" : "text" } 
          pattern = { props.type === "number" ? "[0-9]+([,.][0-9]+)?" : ".*" }
          placeholder={props.placeholder} 
          name={props.name}
          onKeyUp={props.onKeyUp}
          onBlur={props.onBlur}
          onKeyDown={props.onKeyDown}
          list={props.list}
          max={props.max}
          size={props.size}
          readOnly={props.readOnly} />
        { props.icon &&
          <span className={"icon is-small is-right" + ( props.onClickIcon ? " icon-clickable":"")} onClick={props.onClickIcon}>
            <FontAwesomeIcon icon={props.icon} />
          </span>
        }
        {props.info}
      </p>
    </div>
  );
}

const IsActiveContext = React.createContext({ menu_is_active : false, toggleActive: () => {}  });

class NavBar extends React.Component
{
  constructor(props)
  {
    super(props);
    this.state = { menu_is_active  : false };
  }

  menuOnClick = () => {
    this.setState(state => ({
      menu_is_active: !state.menu_is_active
    }));
  }

  render(){
    return(
      <nav className="navbar is-fixed-top is-primary">
        <div className="container">
          <IsActiveContext.Provider value={{menu_is_active: this.state.menu_is_active, toggleActive : this.menuOnClick }}>
            {this.props.children}
          </IsActiveContext.Provider>
        </div>
      </nav>
    );
  }
}

function NavBarBrand(props){
    return <IsActiveContext.Consumer>
            { ({menu_is_active, toggleActive}) =>
              <div className="navbar-brand">
                {props.children}
                { ! props.noBurger &&
                <div role="button" className={"navbar-burger burger" + ( menu_is_active === true ? " is-active" : "" ) } aria-label="menu" aria-expanded="false" data-target="navbarMenu_" onClick={toggleActive}>
                  <span aria-hidden="true"></span>
                  <span aria-hidden="true"></span>
                  <span aria-hidden="true"></span>
                </div> }
              </div> }
          </IsActiveContext.Consumer>;
}

function NavBarMenu(props){
    return <IsActiveContext.Consumer>
            { value =>
              <div className={"navbar-menu" + ( value.menu_is_active === true ? " is-active" : "" ) } id="navbarMenu_" onClick={ props.toggleOnClick ? value.toggleActive : null }>
                {props.children}
              </div> }
          </IsActiveContext.Consumer>;
}

function Cargando(props){
  return (
  <div className="cargando-container">
    <div className="">
      <div className="fa-layers fa-fw">
        <FontAwesomeIcon icon={faCircle} transform="grow-8"/>
        <FontAwesomeIcon icon={faSpinner} spin inverse />
      </div>  
      <span> {props.text ? props.text : "Cargando"} ...</span>
    </div>
  </div>);
}

function ShowDato(props)
{
  return   <div className={props.className}>
            <div>
              <p className="heading">{props.heading}</p>
              <div className={"subtitle " + props.color}>{props.children}</div>
            </div>
          </div>;
}

//depende de Bulma
class Dropdown extends React.Component
{
  constructor(props)
  {
    super();
    this.state = { isActive : false }
  }

  dropdownOn = () => this.setState({isActive: true});

  dropdownOff = () => this.setState({isActive: false});

  render()
  {
    const isActve = this.state.isActive
    return  <div className={"dropdown " + ( isActve ? "is-active" : "" )}>
              {this.props.children({dropdownOn: this.dropdownOn, dropdownOff: this.dropdownOff})}
            </div>
  }
}

function DropdownTrigger(props)
{
  return  <div onFocus={props.dropdownOn} onBlur={() => setTimeout(props.dropdownOff, 150)} className="dropdown-trigger">
            {props.children}
          </div>;
}

function DropDownMenu(props)
{
  return  <>
            {(props.children && props.children.length > 0 ) && 
            <div className="dropdown-menu" id="dropdown-menu" role="menu" onClick={props.onSelect ? props.onSelect : null}>
              <div className="dropdown-content">
                {props.children}
              </div>
            </div>}
          </>;
}

function ShowInput(props)
{
  return    <div className={props.className}>
              <p className={"heading is-size-7 " + props.headingClass}>{props.heading}</p>
              <div className={"subtitle " + props.color}>{props.children}</div>
            </div>;
}


class DataEpysa
{
  constructor(param_data_url)
  {
    if(param_data_url)
      this.dataUrl = param_data_url;
    else
      this.dataUrl = "https://sienet.epysa.cl/sie-android/Scripts/Inc/data.asp";

    this.ef_empty_nulls 	= true;
    this.controller       = null;
    this.timeout          = null;
  }

  abort = () => { if(this.controller) this.controller.abort(); }

  set data_url(param_data_url)
  {
    this.dataUrl = param_data_url;
  }

  set emptyNulls(true_false)
  {
    this.ef_empty_nulls = ( true_false ?  true : false )
  }

  comillas(params)
  {
    const parameters = {...params};
  	for (var param in parameters)
  	{
  		if ( typeof parameters[param] === 'string' && ( parameters[param] === "" || parameters[param] === null ) && this.ef_empty_nulls  )
        parameters[param] = "null";
      else if ( typeof parameters[param] === 'object' )
        parameters[param] = "'" + JSON.stringify(parameters[param]) + "'";
  		else if( typeof parameters[param] === 'string' && parameters[param].toLowerCase()  !== "null" )
  			parameters[param] = "'" + parameters[param] + "'";
    }
    return parameters
  }

  async exec(procedure, parameters, timeout)
  {
    if (timeout)
      return await new Promise( r => {   
        this.Timeout = setTimeout( () => r(
          this._exec(procedure,parameters)
        ), timeout);
      });
    else
      return await this._exec(procedure,parameters);
  }



  async _exec(procedure, parameters)
  {
    let param = "";

    this.controller = new AbortController();

    if (parameters === null || parameters === undefined)
      parameters = {};

  	const newParam = this.comillas(parameters);

  	newParam.procedure = "EXEC " + procedure;

    for (let par in newParam)
    {
      const value = newParam[par] === null || newParam[par] === undefined ? "null" : newParam[par] 
      param  = param + par + "=" + value + "&";
    }

    return await fetch( this.dataUrl,
      {
        method 	: 'POST',
        headers : { 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' },
        body    : encodeURI(param),
        signal  : this.controller.signal
      });
  }

}

function toMoney(n){
  if (n || n === 0)
    if ( isNaN(toNumber(n)) )
      return n;
    else
      return Number(toNumber(n).toFixed(0)).toLocaleString();
  else 
    return "";
}

function toNumber(str){

  let result;
  ////console.log("IN: " + str + " " + typeof str)
  if (typeof str === "string" )
  {
    const str_limpio  = str.replace( /,/g, "" )
    if (! isNaN(str_limpio) && str.slice(-1) !== "." && str.length > 0 ){
      const numero = Number( str_limpio );
      result = isNaN(numero) ? str : numero;
    } 
    else
      result = str;
  }
  else result = str;
  ////console.log("OUT: " + result + " " + typeof result)
  return result;
}

function toBinaryHex(str)
{
  return str.split("").reduce((hex,c)=>hex+c.charCodeAt(0).toString(16).padStart(4,"0"),"")
}

function percent(n)
{
  if (n)
    if ( typeof toNumber(n) !== "number" )
      return n;
    else
      return Number( Number(n).toFixed(2)  );
  else 
    return "";
}


function string_time(hr, mn)
{
  const horas   = hr ? hr : 0;
  const minutos = mn ? mn : 0;
  return horas + ":" + ( minutos < 10 ? "0" +  minutos : minutos);
}


function Modal(props)
{

  return  <div className={"modal " + ( props.isActive ? "is-active" : "" )  }>
            <div className="modal-background" onClick={props.toggleActive}></div>
            <div className="modal-content">
              {props.children}
            </div>
          </div>;
}

function NoImage()
{
  return  <div className="icon is-large has-text-grey-lighter">
            <FontAwesomeIcon icon={faImage} size="2x" />
          </div>
}

export { Input, NavBar, NavBarBrand, DataEpysa, NavBarMenu, Dropdown, DropDownMenu, DropdownTrigger, toMoney, Cargando, Modal, toBinaryHex, ShowDato, ShowInput, percent, toNumber, string_time, NoImage}